<template>
    <div class="table">
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <!-- <el-form-item label="车主名称" :inline="true">
                    <el-input v-model.trim="filters.movieName" placeholder="请输入车主名称"></el-input>
                </el-form-item>
                <el-form-item label="车辆品牌">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="办理人">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="工单状态">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button>查询</el-button> -->
                <el-button type="primary" icon="plus" @click="chooseCar">新建</el-button>
                <!-- <el-button type="primary" icon="plus">导出</el-button> -->
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="vin_number" label="vin号" width="120">
                </el-table-column>
                <el-table-column prop="band_name" label="车主名称" width="120">
                </el-table-column>
                <el-table-column prop="car_brand" label="车辆品牌" width="120">
                </el-table-column>
                <el-table-column prop="car_series" label="车辆系列" width="120">
                </el-table-column>
                <el-table-column prop="tran_type" label="办理人" width="120">
                    <template slot-scope="{row}">
                        {{row.tran_type == 1 ? '公司人员' : '专业人员'}}
                    </template>
                </el-table-column>
                <el-table-column prop="work_status_name" label="工单状态" width="120">
                </el-table-column>
                <el-table-column prop="operater" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                    <template slot-scope="{row}">
                        {{row.create_time | time}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button
                            type="text"
                            icon="el-icon-view"
                            @click="$router.push({path: '/newCarDoor/view', query: { id: row.id }})">
                            查看
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/newCarDoor/edit', query: { id: row.id }})"
                            v-if="isShowEdit(row)">
                            修改
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/newCarDoor/edit', query: { id: row.id }})"
                            v-if="isShowAudit(row)">
                            审核
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleDelete(row, $index)"
                            v-if="isShowDel(row)">
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination layout="prev, pager, next, jumper" @current-change="handleCurrentChange" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
        
        <!-- 选择车辆 -->
        <el-dialog title="选择车辆" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import selectCar from '@/components/selectCar'
    import { mapGetters } from 'vuex'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 0,
                loading: true,
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        plate_number: true,
                        vin_number: true,
                        band_name: true,
                        car_info: true
                    },
                    formConfig: {
                        val: false
                    },
                    addCar: true,
                    pageType: 'insuranceOrder'
                    
                },
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        components:{
            selectCar
        },
        created() {
            this.getData();
        },
        computed: {
            ...mapGetters([
                'userinfo'
            ])
        },
        methods: {
            isShowEdit(row){
                if(row.node_id==83 || row.node_id==0){
                    return row.handle==1
                }
            },
            isShowAudit(row){
                return row.node_id!=83 && row.node_id!=108 && row.auth_user==1
            },
            isShowDel(row){
                return row.node_id==83
            },
            // 选择车辆
            chooseCar(){
                this.dialogSelectCarType = true
            },
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params= {
                    page: this.page,
                    ...this.filters
                }
                this.loading=true
                API.apiNewCarDoorList(params).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        this.tableData = res.data.data;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            search() {
                this.isSearch = true;
            },
            handleDelete(row, index) {
                API.apiNewCarDoorDel({id:row.id}).then(res => {
                    if(res.code == 0){
                        this.tableData.splice(index, 1);
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                let params = {
                    bus_id: row.id,
                    creater_id: this.userinfo.user_id,
                    creater: this.userinfo.user_name
                }
                API.apiNewCarDoorAdd(params).then(res => {
                    if(res.code == 0){
                        this.dialogSelectCarType = false;
                        this.getData();
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }

</script>

